<template>

  <div class="container">
<div class="tqyb">
  <img src="../assets/img/tqyb.gif" alt="">
</div>
    <!-- 搜索框 -->
    <div class="serch">
      <input
        class="shuru"
        type="text"
        placeholder="输入正确的城市/县城名称"
        v-model="city"
        @keyup.enter="serchWeather"
      />
 
      <button class="serchbt" @click="serchWeather" @mouseenter="jingguo" @mouseleave="yichu" >搜索</button>
 
    </div>

              <!-- 热点部分 -->
    <div class="bsgs">
      <div class="rd">
       <span @click="changeCity('北京')">北京</span>
      <span @click="changeCity('上海')">上海</span>
      <span @click="changeCity('广州')">广州</span>
      <span @click="changeCity('深圳')">深圳</span>
      </div>
  </div>
  	    
<div class="txbox">
  <!-- 提示 -->
        <div class="wxts">
      <div class="imgs"> <img src="../assets/img/hh.gif" alt="" ></div>
        <div class="infos">	   
			<p> {{ganmao}} </p>	
		 </div>
     </div>
    <h3 class="tianshu">
        最近
        <strong class="str">{{ this.weatherList.length }}天</strong>
        的天气预报
      </h3>
  </div>      
    <!-- 天气展示 -->

    <div class="xsbox">
   
     
    <div class="tianqijg">
      <ul>
        <li v-for="(item, index) in weatherList" :key="index">
          <h3>{{ item.type }}</h3>
          <span>{{ item.high }}</span>
          <span>{{ item.low }}</span>
          <span>{{ item.fengxiang }}</span>
          <p>{{ item.date }}</p>
        </li>
      </ul>
    </div>
</div>
  </div>

</template>

<script>
export default {
  name: "Tianqi",
  data() {
    return {
      city: "",
      weatherList: [],
      isShow: false,
      ganmao: "",
    };
  },
  methods: {
    // 查询天气
    serchWeather: function (city) {
      console.log(this.city);
      var that = this;
      this.$axios
        .get("http://wthrcdn.etouch.cn/weather_mini?city=" + this.city)
        .then((res) => {
          console.log(res.data);
          that.isShow = false;
          that.weatherList = res.data.data.forecast;
          console.log(res.data.data.ganmao);
          that.ganmao = res.data.data.ganmao;
          // 成功处理
		})
		// 失败
        .catch(function (err) {
                    alert('请输入正确的城市或县城');
                })
    },
    changeCity: function (city) {
      this.city = city;
      this.serchWeather();
    },
       jingguo: function ($event) {
            // console.log('鼠标经过');
            $event.currentTarget.className = "serchbt mouse";

        },
        
        yichu: function ($event) {
            // console.log('鼠标移出');
            $event.currentTarget.className = "serchbt";
        }
  },
  mounted() {
    this.serchWeather();
  },

    };

</script>

